import { ReactNode } from 'react';
import { Link } from 'react-router-dom';
import useContact from '../../../hooks/contact/useContact';
import useImage from '../../../hooks/media/useImage';
import { ContactConfig } from '../../../provider/contact/ContactTypes';
import FallbackImg from '../../FallbackImg/FallbackImg';
import LoadingParagraph from '../../Loaders/LoadingParagraph/LoadingParagraph';

const PersonCard = ({
  dotYouId,
  href,
  isChecked,
  className,
  children,
  onClick,
}: {
  dotYouId: string;
  href?: string;
  isChecked?: boolean;
  className: string;
  children?: ReactNode;
  onClick?: React.MouseEventHandler<HTMLDivElement>;
}) => {
  const Wrapper = ({ children }) => (href ? <Link to={href}>{children}</Link> : <>{children}</>);
  const { data: contactData, isLoading } = useContact({
    dotYouId: dotYouId,
  }).fetch;
  const { data: imageUrl } = useImage(
    contactData?.imageFileId || undefined,
    ContactConfig.ContactTargetDrive
  ).fetch;

  if (isLoading) {
    return <LoadingParagraph className={`aspect-[3/5] ${className}`} />;
  }

  const nameData = contactData?.name;
  const fullName = nameData ? `${nameData.givenName ?? ''} ${nameData.surname ?? ''}` : dotYouId;
  const getInitials = () => {
    if (nameData) {
      return nameData.givenName?.[0] + nameData.surname?.[0] + '';
    }

    const splittedDomain = dotYouId?.split('.');
    if (splittedDomain?.length >= 2) {
      return splittedDomain[0][0] + splittedDomain[1][0] + '';
    }

    return '--';
  };

  return (
    <div className={className}>
      <Wrapper>
        <div
          className={`h-full rounded-md border bg-white transition-colors  ${
            isChecked
              ? 'border-4 border-indigo-500 dark:border-indigo-700'
              : isChecked === false
              ? 'border-4'
              : 'border-gray-200 border-opacity-60 dark:border-gray-800 dark:bg-gray-800'
          }`}
          onClick={onClick}
        >
          <div className="aspect-square">
            {'image' in contactData && contactData.image ? (
              <figure className={'relative overflow-hidden'}>
                <img
                  src={contactData.image.url}
                  width={contactData.image.pixelWidth}
                  height={contactData.image.pixelHeight}
                  className="aspect-square w-full object-cover"
                />
              </figure>
            ) : contactData.imageFileId ? (
              <figure className={'relative overflow-hidden'}>
                <img src={imageUrl} className="aspect-square object-cover " />
              </figure>
            ) : (
              <FallbackImg initials={getInitials()} />
            )}
          </div>
          <div className="p-2">
            <h2 className="font-thiner mb-6 dark:text-white">{fullName ?? dotYouId}</h2>
            {children}
          </div>
        </div>
      </Wrapper>
    </div>
  );
};

export default PersonCard;
