import { useState } from 'react';
import { useParams } from 'react-router-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useCircle from '../../../hooks/circle/useCircle';
import Alert from '../../Alerts/Alert/Alert';
import ActionButton, { mergeStates } from '../../Buttons/ActionButton';
import PersonCard from '../../Connection/PersonCard/PersonCard';
import CircleDialog from '../../Dialog/CircleDialog/CircleDialog';
import MemberLookupDialog from '../../Dialog/MemberLookupDialog/MemberLookupDialog';
import Circles from '../../Icons/Circles/Circles';
import PageMeta from '../../Layout/PageMeta/PageMeta';
import LoadingDetailPage from '../../Loaders/LoadingDetailPage/LoadingDetailPage';
import DrivePermissionView from '../../PermissionViews/DrivePermissionView/DrivePermissionView';
import Section from '../../Sections/Section';

const CircleDetails = () => {
  const { circleKey } = useParams();
  const decodedCircleKey = decodeURIComponent(circleKey);
  const {
    fetch: { data: circle, isLoading: circleLoading },
    fetchMembers: { data: members, isLoading: membersLoading },
    provideGrants: { mutateAsync: addMembers, status: addMemberStatus },
    revokeGrants: { mutateAsync: removeMembers, status: removeMemberStatus },
    enableCircle: { mutate: enableCircle, status: enableCircleStatus },
    disableCircle: { mutate: disableCircle, status: disableCircleStatus },
    removeCircle: { mutateAsync: removeCircle, status: removeCircleStatus },
  } = useCircle({ circleId: decodedCircleKey });
  const [isOpenMemberLookup, setIsOpenMemberLookup] = useState(false);

  const [isOpenEdit, setIsOpenEdit] = useState(false);

  if (circleLoading) {
    return <LoadingDetailPage />;
  }

  if (!circle) {
    return <>No matching circle found</>;
  }

  return (
    <>
      <PageMeta
        icon={Circles}
        title={`${circle.name}`}
        actions={
          <>
            {circle.disabled ? (
              <>
                <ActionButton
                  type="remove"
                  icon="trash"
                  className="m-2"
                  state={removeCircleStatus}
                  onClick={() => {
                    removeCircle({ circleId: circle.id });
                  }}
                  confirmOptions={{
                    title: `${t('Remove Circle')} ${circle.name}`,
                    buttonText: t('Remove'),
                    body: t(
                      'Are you sure you want to remove this circle, all members will lose their access provided by the permissions of this circle?'
                    ),
                  }}
                >
                  {t('Delete')}
                </ActionButton>
                <ActionButton
                  type="primary"
                  className="m-2"
                  onClick={() => {
                    enableCircle({ circleId: circle.id });
                  }}
                  state={enableCircleStatus}
                >
                  {t('Enable Circle')}
                </ActionButton>
              </>
            ) : (
              <ActionButton
                type="remove"
                className="m-2"
                onClick={() => {
                  disableCircle({ circleId: circle.id });
                }}
                state={disableCircleStatus}
                confirmOptions={{
                  title: t('Disable Circle'),
                  buttonText: t('Disable'),
                  body: `${t('Are you sure you want to disable the circle')} ${circle.name}`,
                }}
              >
                {t('Disable Circle')}
              </ActionButton>
            )}
            <ActionButton
              type="primary"
              className="m-2"
              onClick={() => {
                setIsOpenMemberLookup(true);
              }}
            >
              {t('Edit Members')}
            </ActionButton>
            <ActionButton
              type="primary"
              icon="edit"
              className="m-2"
              onClick={() => {
                setIsOpenEdit(true);
              }}
            >
              {t('Edit Circle')}
            </ActionButton>
          </>
        }
        breadCrumbs={[
          { href: '/owner/circles', title: 'My Circles' },
          { title: circle.name ?? '' },
        ]}
      />
      {circle.disabled && (
        <Alert type="critical" title={t('Circle is disabled')} className="mb-5">
          {t(
            'This circle is disabled, all members no longer have the access provided by this cirlce'
          )}
        </Alert>
      )}

      <section>{circle.description}</section>

      <Section
        title={
          <>
            {t('Access on the following drives')}
            <small className="block text-sm text-slate-500">
              {t('all contact added will have this access')}
            </small>
          </>
        }
        isOpaqueBg={true}
      >
        {circle.driveGrants?.length ? (
          <div className="-my-6">
            {circle.driveGrants.map((grant) => {
              return (
                <DrivePermissionView
                  key={`${grant.permissionedDrive.drive.alias}-${grant.permissionedDrive.drive.type}`}
                  driveGrant={grant}
                  className="my-6"
                />
              );
            })}
          </div>
        ) : (
          <div className="flex flex-row">
            <p className="my-auto">{t("This circle doesn't have any access")}</p>
          </div>
        )}
      </Section>

      <Section isOpaqueBg={true} isBorderLess={true} title={t('Members')}>
        {members?.length && !membersLoading ? (
          <div className="-m-1 flex flex-row flex-wrap">
            {members.map((member) => (
              <CircleMemberCard
                key={member}
                circleId={decodedCircleKey}
                dotYouId={member}
                className="w-1/2 p-1 sm:w-1/2 md:w-1/3 lg:w-1/4 xl:w-1/6"
              />
            ))}
          </div>
        ) : (
          <div className="flex flex-row">
            <p className="my-auto">
              {t('Mmh, this looks like an emtpy circle... Time to add some connections?')}{' '}
            </p>
            <ActionButton
              onClick={(e) => {
                e.preventDefault();
                setIsOpenMemberLookup(true);

                return false;
              }}
              type="secondary"
              className="ml-2"
            >
              {t('Add')}
            </ActionButton>
          </div>
        )}
      </Section>

      <CircleDialog
        title={`${t('Edit')}: ${circle.name}`}
        isOpen={isOpenEdit}
        confirmText={t('Update Circle')}
        defaultValue={circle}
        onCancel={() => {
          setIsOpenEdit(false);
        }}
        onConfirm={() => {
          setIsOpenEdit(false);
        }}
      />
      <MemberLookupDialog
        isOpen={isOpenMemberLookup}
        title={`${t('Add Members to')} ${circle.name}`}
        defaultMembers={members}
        onCancel={() => {
          setIsOpenMemberLookup(false);
        }}
        confirmationStatus={mergeStates(addMemberStatus, removeMemberStatus)}
        onConfirm={async (toProvideMembers, toRevokeMembers) => {
          await addMembers({ circleId: decodedCircleKey, dotYouIds: toProvideMembers });
          await removeMembers({ circleId: decodedCircleKey, dotYouIds: toRevokeMembers });

          setIsOpenMemberLookup(false);
        }}
      />
    </>
  );
};

const CircleMemberCard = ({
  circleId,
  dotYouId,
  className,
}: {
  circleId;
  dotYouId: string;
  className: string;
}) => {
  const { mutate: revokeGrants, status: revokeGrantsStatus } = useCircle({
    circleId: circleId,
  }).revokeGrants;

  return (
    <PersonCard
      className={className}
      dotYouId={dotYouId}
      href={(dotYouId && `/owner/connections/${dotYouId}`) ?? undefined}
    >
      <ActionButton
        type="secondary"
        className="w-full"
        onClick={(e) => {
          e.preventDefault();
          revokeGrants({ circleId, dotYouIds: [dotYouId] });
        }}
        state={revokeGrantsStatus}
      >
        Remove from circle
      </ActionButton>
    </PersonCard>
  );
};

export default CircleDetails;
