import { useQuery } from '@tanstack/react-query';
import { CircleNetworkProvider } from '../../provider/connections/CircleNetworkProvider';
import { CircleNetworkRequestProvider } from '../../provider/connections/CircleNetworkRequestProvider';
import useAuth from '../auth/useAuth';

const useConnections = ({ pageSize }: { pageSize: number } = { pageSize: 10 }) => {
  const { getSharedSecret } = useAuth();
  const circleNetworkRequestProvider = CircleNetworkRequestProvider.getInstance(getSharedSecret());
  const circleNetworkProvider = CircleNetworkProvider.getInstance(getSharedSecret());

  const fetchPendingConnections = async ({ pageSize }: { pageSize: number }) => {
    return await (
      await circleNetworkRequestProvider.getPendingRequests({ pageNumber: 1, pageSize: pageSize })
    ).results;
  };

  const fetchSentRequests = async ({ pageSize }: { pageSize: number }) => {
    return await (
      await circleNetworkRequestProvider.getSentRequests({ pageNumber: 1, pageSize: pageSize })
    ).results;
  };

  const fetchConnections = async ({ pageSize }: { pageSize: number }) => {
    return await (
      await circleNetworkProvider.getConnections({ pageNumber: 1, pageSize: pageSize })
    ).results;
  };

  return {
    fetchPending: useQuery(
      ['pendingConnections', pageSize],
      () => fetchPendingConnections({ pageSize }),
      {
        refetchOnWindowFocus: false,
        onError: (err) => console.error(err),
      }
    ),
    fetchSent: useQuery(['sentRequests', pageSize], () => fetchSentRequests({ pageSize }), {
      refetchOnWindowFocus: false,
      onError: (err) => console.error(err),
    }),
    fetchActive: useQuery(['activeConnections', pageSize], () => fetchConnections({ pageSize }), {
      refetchOnWindowFocus: false,
      onError: (err) => console.error(err),
    }),
  };
};

export default useConnections;
